import React from 'react';
import Grid from '@material-ui/core/Grid';
import {createStyles, makeStyles, Theme, useTheme} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Paper from "@material-ui/core/Paper";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import { useDeps } from "../../../context/DepsContext/DepsContext";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import ImagerInterface from "../../../service/Imager/ImagerInterface";
import PathGenerator from "../../../service/PathGenerator/PathGenerator";
import ClearIcon from '@material-ui/icons/Clear';
import CheckIcon from '@material-ui/icons/Check';
import RepoFactionInterface from "../../../repository/FactionRepository/RepoFactionInterface";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonCell: {
      paddingTop: 0,
      paddingBottom: 0,
      width: 0,
    },
    gridWithTable: {
      [theme.breakpoints.down('xs')]: {
        paddingBottom: theme.spacing(3),
      },
    },
    logo: {
      textAlign: "center",
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    nameCell: {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
    },
    entityLogoCell: {
      paddingTop: 0,
      paddingRight: 0,
      paddingBottom: 0
    },
    entityLogoGrid: {
      display: "flex",
      alignItems: "center",
    },
    entityLogoImg: {
      marginRight: theme.spacing(1),
      [theme.breakpoints.down('xs')]: {
        display: "none",
      },
    },
    successColorText: {
      color: theme.palette.success.main,
    },
    errorColorText: {
      color: theme.palette.error.main,
    },
  }),
);

export default function FactionInfoContent(
  {
    content
  }: {
    content: RepoFactionInterface
  }
) {
  let classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    imager,
    pathGenerator,
  }: {
    imager: ImagerInterface,
    pathGenerator: PathGenerator,
  } = useDeps();

  return (
    <React.Fragment>
      <Grid container spacing={isSmall ? 0 : 3}>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <TableContainer component={Paper}>
            <Table aria-label="faction information">
              <TableBody>

                <TableRow>
                  <TableCell
                    colSpan={3}
                    align="center"
                    className={classes.nameCell}
                  >
                    <Typography variant="h6">{ content.name }</Typography>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Corporation:</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    <Grid container>
                      <Grid item className={classes.entityLogoGrid}>
                        <img
                          src={imager.getCorporation(`${content.corporation_id}`, 64)}
                          alt="corporation logo"
                          height={48}
                          width={48}
                          className={classes.entityLogoImg}
                        />
                      </Grid>
                      <Grid item className={classes.entityLogoGrid}>
                        { content.corporation_name }
                      </Grid>
                    </Grid>
                  </TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open corporation"
                      component={Link}
                      to={pathGenerator.corporationInfo(`${content.corporation_id}`)}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                {content.militia_corporation_id !== undefined &&
                  <TableRow>
                    <TableCell>Militia corporation:</TableCell>
                    <TableCell className={classes.entityLogoCell}>
                      <Grid container>
                        <Grid item className={classes.entityLogoGrid}>
                          <img
                            src={imager.getCorporation(`${content.militia_corporation_id}`, 64)}
                            alt="corporation logo"
                            height={48}
                            width={48}
                            className={classes.entityLogoImg}
                          />
                        </Grid>
                        <Grid item className={classes.entityLogoGrid}>
                          { content.militia_corporation_name }
                        </Grid>
                      </Grid>
                    </TableCell>
                    <TableCell align="right" className={classes.buttonCell}>
                      <IconButton
                        size="small"
                        aria-label="open militia corporation"
                        component={Link}
                        to={pathGenerator.corporationInfo(`${content.militia_corporation_id}`)}
                      >
                        <ArrowRightIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                }

                {content.solar_system_id !== undefined &&
                <TableRow>
                    <TableCell>System:</TableCell>
                    <TableCell>{content.solar_system_name}</TableCell>
                    <TableCell align="right" className={classes.buttonCell}>
                      <IconButton
                        size="small"
                        aria-label="open system"
                        component={Link}
                        to={pathGenerator.systemInfo(`${content.solar_system_id}`)}
                      >
                        <ArrowRightIcon/>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                }

                <TableRow>
                  <TableCell>Size factor:</TableCell>
                  <TableCell>{ content.size_factor }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Station count:</TableCell>
                  <TableCell>{ content.station_count }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Station system count:</TableCell>
                  <TableCell>{ content.station_system_count }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Unique:</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    {content.is_unique
                      ? <CheckIcon className={classes.successColorText} />
                      : <ClearIcon className={classes.errorColorText} />
                    }
                  </TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>ID:</TableCell>
                  <TableCell>{ content.faction_id }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>
        </Grid>

        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <TableContainer component={Paper}>
            <Table aria-label="faction description">
              <TableBody>
                <TableRow>
                  <TableCell>
                    <Typography variant="h6">Description</Typography>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>
                    { content.description }
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
