import React from "react";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import TableRow from '@material-ui/core/TableRow';
import Slide from '@material-ui/core/Slide';

import { useDeps } from "../../../context/DepsContext/DepsContext";
import RepoRaceInterface from "../../../repository/RaceRepository/RepoRaceInterface";
import PathGenerator from "../../../service/PathGenerator/PathGenerator";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonCell: {
      paddingTop: 0,
      paddingBottom: 0,
      width: 0,
    },
    content: {
      transition: "unset",
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
  }),
);

const transitionNone = { transition: "unset" };

function MyAccordion(
  {
    race,
    opened
  }: {
    race: RepoRaceInterface,
    opened: string
  }
) {
  const classes = useStyles();
  let {
    pathGenerator
  }: {
    pathGenerator: PathGenerator
  } = useDeps();

  let expanded = opened === `${race.race_id}`;

  return (
    <Accordion
      defaultExpanded={expanded}
      TransitionProps={{ timeout: { appear: 0, enter: 0, exit: 0 } }}
      style={transitionNone}
    >
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
        style={transitionNone}
        classes={{ content: classes.content }}
      >
        <Typography className={classes.heading}>{race.name}</Typography>
      </AccordionSummary>
      <AccordionDetails style={transitionNone}>

          <TableContainer >
            <Table aria-label="race information">
              <TableBody>

                <TableRow>
                  <TableCell>Faction:</TableCell>
                  <TableCell>{ race.alliance_name }</TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open character"
                      component={Link}
                      to={pathGenerator.factionInfo(`${race.alliance_id}`)}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>ID:</TableCell>
                  <TableCell>{ race.race_id }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell style={{ border: 0 }} colSpan={3}>{race.description}</TableCell>
                </TableRow>

              </TableBody>
            </Table>
          </TableContainer>


      </AccordionDetails>
    </Accordion>
  );
}

export default function RacesAccordion(
  {
    races,
    opened
  }: {
    races: RepoRaceInterface[],
    opened: string
  }
) {
  return (
    <Container maxWidth="sm" style={{ padding: 0 }}>
      {races.map((race, index) => (
        <MyAccordion race={race} key={index} opened={opened} />
      ))}
    </Container>
  );
}
