import React, {useEffect, useState} from "react";
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';

import { useDeps } from "../../context/DepsContext/DepsContext";
import AppBar from "../AppBar/AppBar";
import AllianceRepositoryInterface from "../../repository/AllianceRepository/AllianceRepositoryInterface";
import LoadingFullPage from "../LoadingFullPage/LoadingFullPage";
import AlliancesTable from "./AlliancesTable/AlliancesTable";
import ApiErrorFullPage from "../ApiErrorFullPage/ApiErrorFullPage";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        paddingLeft: 0,
        paddingRight: 0
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

function AllianceList() {
  const classes = useStyles();
  let [apiResult, setApiResult] = useState(<LoadingFullPage />);
  let [retry, setRetry] = useState(false);
  let {
    allianceRepository
  }: {
    allianceRepository: AllianceRepositoryInterface
  } = useDeps();

  useEffect(() => {
    allianceRepository.getList()
      .then(result => setApiResult(<AlliancesTable alliances={result} />))
      .catch(e => {
        setApiResult(
          <ApiErrorFullPage
            retry={() => setRetry(true)}
            text={e.message}
          />
        );
      });
  }, [retry]);

  return (
    <React.Fragment>
      <AppBar title="Alliances" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {apiResult}
      </main>
    </React.Fragment>
  );
}

export default AllianceList;
