import React from 'react';
import { useParams } from "react-router-dom";
import RaceList from "../RaceList/RaceList";

export default function RaceInfo() {
  let { id } = useParams<Record<string, string | undefined>>();
  if (id === undefined) {
    id = "";
  }

  return (
    <RaceList opened={id} />
  );
}
