import React from "react";
import MUIDataTable, {MUIDataTableColumnDef, MUIDataTableMeta, MUIDataTableOptions} from "mui-datatables";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import Container from "@material-ui/core/Container";

import { useDeps } from "../../../context/DepsContext/DepsContext";
import RepoFactionInterface from "../../../repository/FactionRepository/RepoFactionInterface";
import PathGenerator from "../../../service/PathGenerator/PathGenerator";


const options: MUIDataTableOptions = {
  elevation: 1,
  filter: false,
  fixedHeader: false,
  print: false,
  responsive: "standard",
  rowHover: false,
  rowsPerPageOptions: [],
  selectableRows: "none",
  sortOrder: {
    name: "name",
    direction: 'asc'
  },
  viewColumns: false,
};

const extractCorpID = (tableMeta: MUIDataTableMeta): number => {
  return tableMeta.rowData[1];
};

export default function FactionsTable(
  {
    factions
  }: {
    factions: RepoFactionInterface[]
  }
) {
  let {
    pathGenerator
  }: {
    pathGenerator: PathGenerator
  } = useDeps();

  const columns: MUIDataTableColumnDef[] = [
    {
      name: "name",
      label: "Name",
      options: {
        customHeadRender: () => null,
      }
    },
    {
      name: "faction_id",
      label: "ID",
      options: {
        customHeadRender: () => null,
        display: false,
      }
    },
    {
      name: "icon",
      label: "",
      options: {
        customHeadRender: () => null,
        customBodyRender: (value, tableMeta, updateValue) => {
          let id = extractCorpID(tableMeta);

          return (
            <IconButton
              size="small"
              aria-label="open"
              component={Link}
              to={pathGenerator.factionInfo(`${id}`)}
            >
              <ArrowRightIcon />
            </IconButton>
          );
        },
        download: false,
        setCellProps: () => ({ style: { width: 0, paddingTop: 0, paddingBottom: 0 }}),
      }
    }
  ];

  return (
    <Container maxWidth="sm" style={{ padding: 0 }}>
      <MUIDataTable
        title=""
        data={factions}
        columns={columns}
        options={options}
      />
    </Container>
  );
}
