import ImagerInterface, { SupportedSizes } from "./ImagerInterface";


class Imager implements ImagerInterface {
  private readonly _baseUrl = "https://images.evetech.net/";

  getAlliance(id: string, size: SupportedSizes | null = null): string {
    return `${this._baseUrl}alliances/${id}/logo${this._getQuery(size)}`;
  }

  getCharacter(id: string, size: SupportedSizes | null = null): string {
    return `${this._baseUrl}characters/${id}/portrait${this._getQuery(size)}`;
  }

  getCorporation(id: string, size: SupportedSizes | null = null): string {
    return `${this._baseUrl}corporations/${id}/logo${this._getQuery(size)}`;
  }

  getTypeIcon(id: string, size: SupportedSizes | null = null): string {
    return `${this._baseUrl}type/${id}/icon${this._getQuery(size)}`;
  }

  getTypeRender(id: string, size: SupportedSizes | null = null): string {
    return `${this._baseUrl}type/${id}/render${this._getQuery(size)}`;
  }

  private _getQuery(size: SupportedSizes | null): string {
    return size === null ? "" : `?size=${size}`;
  }
}

export default Imager;
