import React, {useEffect, useState} from "react";
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';

import { useDeps } from "../../context/DepsContext/DepsContext";
import AppBar from "../AppBar/AppBar";
import LoadingFullPage from "../LoadingFullPage/LoadingFullPage";
import ApiErrorFullPage from "../ApiErrorFullPage/ApiErrorFullPage";
import RaceRepositoryInterface from "../../repository/RaceRepository/RaceRepositoryInterface";
import RacesAccordion from "./RacesAccordion/RacesAccordion";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        paddingLeft: 0,
        paddingRight: 0
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function RaceList(
  {
    opened = "1"
  }: {
    opened?: string
  }
) {
  const classes = useStyles();
  let [apiResult, setApiResult] = useState(<LoadingFullPage />);
  let [retry, setRetry] = useState(false);
  let {
    raceRepository
  }: {
    raceRepository: RaceRepositoryInterface
  } = useDeps();

  useEffect(() => {
    raceRepository.getAll()
      .then(result => setApiResult(<RacesAccordion races={result} opened={opened} />))
      .catch(e => {
        setApiResult(
          <ApiErrorFullPage
            retry={() => setRetry(true)}
            text={e.message}
          />
        );
      });
  }, [retry]);

  return (
    <React.Fragment>
      <AppBar title="Races" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {apiResult}
      </main>
    </React.Fragment>
  );
}
