import React from 'react';
import {createStyles, makeStyles, Theme, useTheme} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Typography from "@material-ui/core/Typography";

import RepoAllianceInterface from "../../../repository/AllianceRepository/RepoAllianceInterface";
import CorpListTable from "../CorpListTable/CorpListTable";
import { useDeps } from "../../../context/DepsContext/DepsContext";
import ImagerInterface from "../../../service/Imager/ImagerInterface";
import DateFormatterInterface from "../../../service/DateFormatter/DateFormatterInterface";
import PathGenerator from "../../../service/PathGenerator/PathGenerator";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonCell: {
      paddingTop: 0,
      paddingBottom: 0,
      width: 0,
    },
    entityLogoCell: {
      paddingTop: 0,
      paddingRight: 0,
      paddingBottom: 0
    },
    entityLogoGrid: {
      display: "flex",
      alignItems: "center",
    },
    entityLogoImg: {
      marginRight: theme.spacing(1),
      [theme.breakpoints.down('xs')]: {
        display: "none",
      },
    },
    gridContainer: {
      marginTop: 0,
    },
    gridWithTable: {
      [theme.breakpoints.down('xs')]: {
        paddingBottom: theme.spacing(3),
      },
    },
    title: {
      [theme.breakpoints.down('xs')]: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
      },
    },
    logo: {
      textAlign: "center",
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    nameCell: {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.secondary.contrastText,
    }
  }),
);

export default function AllianceInfoContent(
  {
    content
  }: {
    content: RepoAllianceInterface
  }
) {
  let classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    dateFormatter,
    imager,
    pathGenerator,
  }: {
    dateFormatter: DateFormatterInterface,
    imager: ImagerInterface,
    pathGenerator: PathGenerator,
  } = useDeps();

  return (
    <React.Fragment>
      <Grid container spacing={isSmall ? 0 : 3} className={classes.gridContainer}>
        <Grid item xs={12} className={classes.logo}>
          <img
            alt="alliance logo"
            src={imager.getAlliance(`${content.id}`)}
            height="180"
            width="180"
          />
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <TableContainer component={Paper}>
            <Table aria-label="alliance information">
              <TableBody>
                <TableRow>
                  <TableCell
                    colSpan={3}
                    align="center"
                    className={classes.nameCell}
                  >
                    <Typography variant="h6">{ content.name }</Typography>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Ticker:</TableCell>
                  <TableCell>{ content.ticker }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>ID:</TableCell>
                  <TableCell>{ content.id }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Creator (char):</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    <Grid container>
                      <Grid item className={classes.entityLogoGrid}>
                        <img
                          src={imager.getCharacter(`${content.creator_id}`, 64)}
                          alt="character portrait"
                          height={48}
                          width={48}
                          className={classes.entityLogoImg}
                        />
                      </Grid>
                      <Grid item className={classes.entityLogoGrid}>
                        { content.creator_name }
                      </Grid>
                    </Grid>
                  </TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open character"
                      component={Link}
                      to={pathGenerator.characterInfo(`${content.creator_id}`)}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Creator (corp):</TableCell>
                  <TableCell className={classes.entityLogoCell}>
                    <Grid container>
                      <Grid item className={classes.entityLogoGrid}>
                        <img
                          src={imager.getCorporation(`${content.creator_corporation_id}`, 64)}
                          alt="corporation logo"
                          height={48}
                          width={48}
                          className={classes.entityLogoImg}
                        />
                      </Grid>
                      <Grid item className={classes.entityLogoGrid}>
                        { content.creator_corporation_name }
                      </Grid>
                    </Grid>
                  </TableCell>
                  <TableCell align="right" className={classes.buttonCell}>
                    <IconButton
                      size="small"
                      aria-label="open corporation"
                      component={Link}
                      to={pathGenerator.corporationInfo(`${content.creator_corporation_id}`)}
                    >
                      <ArrowRightIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>

                <TableRow>
                  <TableCell>Date founded:</TableCell>
                  <TableCell>{ dateFormatter.withDateTime(new Date(content.date_founded)) }</TableCell>
                  <TableCell align="right"> </TableCell>
                </TableRow>
                {content.executor_corporation_name !== undefined &&
                  <TableRow>
                      <TableCell>Executor:</TableCell>
                      <TableCell className={classes.entityLogoCell}>
                        <Grid container>
                          <Grid item className={classes.entityLogoGrid}>
                            <img
                              src={imager.getCorporation(`${content.executor_corporation_id}`, 64)}
                              alt="corporation logo"
                              height={48}
                              width={48}
                              className={classes.entityLogoImg}
                            />
                          </Grid>
                          <Grid item className={classes.entityLogoGrid}>
                            { content.executor_corporation_name }
                          </Grid>
                        </Grid>
                      </TableCell>
                      <TableCell align="right" className={classes.buttonCell}>
                          <IconButton
                              size="small"
                              aria-label="open character"
                              component={Link}
                              to={pathGenerator.corporationInfo(`${content.executor_corporation_id}`)}
                          >
                              <ArrowRightIcon />
                          </IconButton>
                      </TableCell>
                  </TableRow>
                }
                {content.faction_name !== undefined &&
                  <TableRow>
                    <TableCell>Faction:</TableCell>
                    <TableCell>{ content.faction_name }</TableCell>
                      <TableCell align="right" className={classes.buttonCell}>
                        <IconButton
                          size="small"
                          aria-label="open character"
                          component={Link}
                          to={pathGenerator.factionInfo(`${content.faction_id}`)}
                        >
                          <ArrowRightIcon />
                        </IconButton>
                    </TableCell>
                  </TableRow>
                }
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid item xs={12} md={6} className={classes.gridWithTable}>
          <CorpListTable corpList={content.corporations} />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
