// Intentionally not using an interface
class PathGenerator {
  public readonly base = {
    allianceList: "/alliance/",
    allianceInfo: "/alliance/:id",
    ancestryInfo: "/ancestry/:id",
    bloodlineInfo: "/bloodline/:id",
    characterInfo: "/character/:id",
    constellationInfo: "/constellation/:id",
    corporationInfo: "/corporation/:id",
    factionInfo: "/faction/:id",
    factionList: "/faction/",
    home: "/",
    raceList: "/race/",
    raceInfo: "/race/:id",
    regionInfo: "/region/:id",
    search: "/search",
    systemInfo: "/system/:id",
    stationInfo: "/station/:id",
    test: "/test",
    typeInfo: "/type/:id",
  };
  private readonly _idPlaceholder = ":id";

  public allianceInfo(id: string): string {
    return this.base.allianceInfo.replace(this._idPlaceholder, id);
  }

  public allianceList(): string {
    return this.base.allianceList;
  }

  public ancestryInfo(id: string): string {
    return this.base.ancestryInfo.replace(this._idPlaceholder, id);
  }

  public bloodlineInfo(id: string): string {
    return this.base.bloodlineInfo.replace(this._idPlaceholder, id);
  }

  public characterInfo(id: string): string {
    return this.base.characterInfo.replace(this._idPlaceholder, id);
  }

  public corporationInfo(id: string): string {
    return this.base.corporationInfo.replace(this._idPlaceholder, id);
  }

  public factionInfo(id: string): string {
    return this.base.factionInfo.replace(this._idPlaceholder, id);
  }

  public factionList(): string {
    return this.base.factionList;
  }

  public raceInfo(id: string): string {
    return this.base.raceInfo.replace(this._idPlaceholder, id);
  }

  public raceList(): string {
    return this.base.raceList;
  }

  public systemInfo(id: string): string {
    return this.base.systemInfo.replace(this._idPlaceholder, id);
  }

  public stationInfo(id: string): string {
    return this.base.stationInfo.replace(this._idPlaceholder, id);
  }
}

export default PathGenerator;
